<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <section class="row">
    <div class="card border-dark">
      {{tempData}}
      <label for="exampleDataList" class="form-label">Datalist example</label>
      <input
        class="form-control"
        list="datalistOptions"
        id="exampleDataList"
        v-model="tempData"
        placeholder="Type to search..."
      />
      <datalist id="datalistOptions">
        <option value="San Francisco"></option>
        <option value="New York"></option>
        <option value="Seattle"></option>
        <option value="Los Angeles"></option>
        <option value="Chicago"></option>
        <template v-for="(item, index) in optionList" :key="index">
          <option :value="item.title"></option>
        </template>
      </datalist>
    </div>
    <div class="card border-dark paperDarkBg">
      <div class="card-body d-flex align-items-start text-uppercase">
        <span class="h4 me-3">step 1</span
        ><span class="h3" style="font-family: BellGothicStd">Login</span>
      </div>
    </div>
    <div class="card border-dark col-6">
      <div class="card-body">
        <VForm
          v-slot="{ meta }"
          @submit="logIn"
          class="d-flex flex-column h-100"
        >
          <div class="form-floating mb-3">
            <VField
              name="email"
              type="email"
              rules="required|email"
              class="form-control"
              id="floatingInput"
              placeholder="name@example.com"
            />
            <ErrorMessage name="email" class="text-danger" />
            <label for="floatingInput">Email address</label>
          </div>
          <div class="form-floating mb-3">
            <VField
              name="password"
              type="password"
              rules="required"
              class="form-control"
              id="floatingPassword"
              placeholder="name@example.com"
            />
            <ErrorMessage name="password" class="text-danger" />
            <label for="floatingPassword">Password</label>
          </div>
          <div class="form-floating text-center mt-auto">
            <button class="btn btn-dark" :disabled="!meta.valid" type="submit">
              Sign In
            </button>
          </div>
        </VForm>
      </div>
    </div>
    <div class="card border-dark col-6">
      <div class="card-body">
        <VForm v-slot="{ meta }" class="d-flex flex-column h-100">
          <div class="form-floating mb-3">
            <VField
              name="email"
              type="email"
              rules="required|email"
              class="form-control"
              id="floatingInput1"
              placeholder="name@example.com"
            />
            <ErrorMessage name="email" class="text-danger" />
            <label for="floatingInput1">Email address</label>
          </div>
          <div class="form-floating text-center mt-auto">
            <component
              :is="'wired-button'"
              class="bg-dark text-light h4 mb-0"
              type="submit"
              :disabled="!meta.valid"
            >
              forget password
            </component>
          </div>
        </VForm>
      </div>
    </div>
  </section>
</template>

<script>
import { apiSignIn } from '@/assets/javascript/api';
import { getToken } from '@/assets/javascript/methods';

export default {
  data() {
    return {
      tempData: '',
      optionList: [{ title: 'lulu' }, { title: '臭臭' }, { title: '西8老馬' }],
    };
  },
  methods: {
    logIn(e) {
      this.$emitter.emit('loadingStatus', true);
      apiSignIn(e)
        .then((res) => {
          const expries = new Date(Date.now() + 1000 * 60 * 60 * 24).toGMTString();
          document.cookie = `LULU-Bake-Admin=${res.data.data.token}; expires=${expries}; path=/`;
          this.$emitter.emit('loadingStatus', false);
          this.$router.push('/backend/home');
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    checkSignIn() {
      // 取得 cookie 中的 token
      const token = getToken();
      // 若有 token 則登入，反之轉為登入頁面
      if (token) {
        this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
        this.$router.push('/backend/home');
      }
    },
  },
  created() {
    this.checkSignIn();
  },
};
</script>
