import axios from 'axios';

const api = process.env.VUE_APP_API;

// admin
export const apiSignIn = (data) => axios.post(`${api}/admin/sign_in`, data);
export const apiAdminGetProducts = () => axios.get(`${api}/admin/products`);
export const apiAdminCreateProduct = (data) => axios.post(`${api}/admin/product`, data);
export const apiAdminUpdateProduct = (id, data) => axios.patch(`${api}/admin/product/${id}`, data);
export const apiAdminDeleteProduct = (id) => axios.delete(`${api}/admin/product/${id}`);

// product
export const apiGetProducts = () => axios.get(`${api}/products`);
export const apiGetProduct = (id) => axios.get(`${api}/product/${id}`);

// order
export const apiGetOrder = (id) => axios.get(`${api}/order/${id}`);
export const apiPostOrder = (data) => axios.post(`${api}/order`, data);
export const apiDeleteOrder = (id) => axios.delete(`${api}/order/${id}`);
export const apiUpdateOrder = (id, data) => axios.patch(`${api}/order/${id}`, data);

// linePay
export const apiGetLinepay = () => axios.get(`${api}/checkout`);
export const apiPostLinepay = (id) => axios.post(`${api}/linepay/${id}`);

// UpLoad
export const apiUpLoad = (data) => axios.post(`${api}/upload?type=""`, data);
// export const apiUpLoad = (data, type) => axios.post(`${api}/upload?type=${type}`, data);
